body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow-x:hidden;
  margin: 0;
  padding: 0;
}

.app {
  font-family: 'Montserrat', sans-serif;  
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.flower {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 20%;
  min-width: 180px;
}

@media screen and (max-width: 500px) {
  .flower {
    display: none;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: 10px 0;
  text-align: center;
  color: #6667ab;
  margin: 0;
}

.header img {
  width: 43%;
  min-width: 300px;
  max-width: 450px;
  z-index: 20;
  margin-top: 30px;
}

.container {
  text-align: center;
  padding: 20px;
}

.title {
  margin: 0;
  font-weight: 600;
  padding: 50px 0;
}

.titleLabel {
  padding: 10px 15px;
  border-bottom: 1px solid #6667ab;
  width: 80%;
}

@media screen and (max-width: 610px) {
  .titleLabel {
    border-bottom: none;
  }

  .title {
    border-bottom: 1px solid #6667ab;
    padding: 50px 0 10px 0;
  }
}

.subject {
  padding: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h4 {
  line-height:1.5;
  font-weight: 500;
  max-width: 660px;
}

.category {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  width: 100%;
  margin-top: 50px;
}

.categoryImage {
  width: 100%;
  max-width: 200px;
}

.items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.avaliable {
  opacity: 1;
  cursor: pointer;
}

.avaliable:hover {
  opacity: 0.75;
}

.unavaliable {
  opacity: 0.55;
  cursor: not-allowed;
}

.item {
  margin: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 250px;
  height: 500px;
  justify-content: space-between;
}

@media screen and (max-width: 720px) {
  .item {
    margin: 30px 0;
  }
}

.itemName {
  font-weight: bold;
  height: 100px;
  display: flex;
  align-items: center;
}

.itemBackground {
  border-radius: 50%;
  background-color: #6667ab;
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin-bottom: 50px; */
}

.itemImage {
  width: 200px;
  height: 200px;
  margin-top: 25px;
}

.inputResponsavel {
  width: 100%;
  height: 50%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

input {
  width: 80%;
  height: 100%;
  border: 1px solid #6667ab;
  padding: 5px 10px;
  font-size: 16px;
  outline: #6667ab;
}

input:focus-visible {
  outline: none;
}

button {
  text-decoration: none;
    border: none;
    background-color: transparent;
    font-size: 20px;
    cursor: pointer;
}

.labelBackground {
  background-color: bisque;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin: 10px 0 30px 0;
  padding: 5px 10px; */
}

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px;
  color: #6667ab;
}

.sugestao-sem-foto,
.sugestao,
.sem-sugestao {
  color: #6667ab;
  text-decoration: none;
  height: 20px;
  font-weight: bold;
}

.content {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100%;
  justify-content: flex-end;
}

